.toggle-button-cover
{
    display: table-cell;
    position: relative;
    width: 150px;
    height: 100px;
    box-sizing: border-box;
}

.button-cover
{
    margin: 10px;
    background-color: transparent;
}

.button-cover, .knobs, .layer
{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.button
{
    position: relative;
    top: 50%;
    width: 67px;
    height: 36px;
    margin: -20px auto 0 auto;
    box-sizing: border-box;
    overflow: hidden;
}

.button.r, .button.r .layer
{
    border-radius: 100px;
}

.button.b2
{
    border-radius: 2px;
}

.checkbox
{
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    opacity: 0;
    cursor: pointer;
    z-index: 3;
}

.knobs
{
    z-index: 2;
}

.layer
{
    width: 100%;
    background-color: rgb(49,41,98);
    -webkit-transition: 0.3s ease all;
    transition: 0.3s ease all;
    z-index: 1;
}

/* Button 1 */
#button-1 .knobs:before
{
    content: '';
    position: absolute;
    top: 3px;
    left: 4px;
    width: 20px;
    height: 10px;
    color: #fff;
    font-size: 10px;
    font-weight: bold;
    line-height: 1;
    text-align: center;
    padding: 15px 15px;
    background-color: #fff;
    border-radius: 50%;
    -webkit-transition: 0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15) all;
    transition: 0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15) all;
}

#button-1 .checkbox:checked + .knobs:before
{
    content: '';
    left: 33px;
    background-color: #fff;
}

#button-1 .checkbox:checked ~ .layer
{
    background-color: rgb(49,41,98);
}

#button-1 .knobs, #button-1 .knobs:before, #button-1 .layer
{
    -webkit-transition: 0.3s ease all;
    transition: 0.3s ease all;
}


.button-c {
    position: relative;
